<template>
    <div class="main">
        <div class="studentFile">
            <div class="searchA">
                <el-date-picker v-model="search.year" clearable value-format="yyyy" size="small" type="year" placeholder="选择年">
                </el-date-picker>
                <el-select v-model="search.month" clearable placeholder="请选择" size="small">
                    <el-option v-for="item in month_list" :label="item.name" :value="item.id" :key="item.id"></el-option>
                </el-select>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
                <div class="add">
                    <el-button size="small" type="primary" @click="addBtn()">新增档案</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :min-width="item.minWidth"></el-table-column>
                    </template>
                    <el-table-column prop="address" align="center" label="操作" fixed="right" width="120">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="edit(scope.row)">编辑</el-button>
                            <el-button type="text" size="small" style="color: #ff2b2b" @click="delBtn(scope.row.id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" v-dialogDrag :visible.sync="dialogVisible" width="60%" :close-on-click-modal="false">
                <el-descriptions :colon="false" class="margin-top" :title="tableTitle" :column="1" direction="vertical">
                    <el-descriptions-item>
                        <span style="margin-right:40px">
                            <el-select v-model="formData.month" style="width:120px" placeholder="请选择">
                                <el-option v-for="item in month_list" :label="item.name" :value="item.id" :key="item.id"></el-option>
                            </el-select>
                            在校表现
                        </span>
                        <span style="margin-right:20px">
                            学年度
                            <el-select v-model="formData.year" style="width:120px" placeholder="请选择">
                                <el-option v-for="(item,index) in year_list" :label="item" :value="item" :key="index"></el-option>
                            </el-select>
                        </span>
                        <span>
                            学期
                            <el-select v-model="formData.term" style="width:120px" placeholder="请选择">
                                <el-option v-for="(item,index) in term_list" :label="item.name" :value="item.id" :key="index"></el-option>
                            </el-select>
                        </span>
                    </el-descriptions-item>
                    <el-descriptions-item label="第一、二周">
                        <el-table :data="tableInfo" ref="tableInfo" border element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" style="width: 100%">
                            <el-table-column prop="achievement" label="重要成绩及表彰" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.achievement" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="discipline" label="重大违纪" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.discipline" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="hold_post" label="担任职务或岗位" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.hold_post" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="home_school" label="家校合作情况" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.home_school" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-descriptions-item>
                    <el-descriptions-item label="第三、四周">
                        <el-table :data="tableInfoTwo" ref="tableInfoTwo" border element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" style="width: 100%">
                            <el-table-column prop="achievement" label="重要成绩及表彰" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.achievement" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="discipline" label="重大违纪" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.discipline" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="hold_post" label="担任职务或岗位" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.hold_post" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="home_school" label="家校合作情况" :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    <el-input v-model="scope.row.home_school" clearable size="small"></el-input>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-descriptions-item>
                    <el-descriptions-item label="个人积分">
                        <el-input style="width:25%" maxlength="10" v-model.number="formData.personal_score" clearable size="small"></el-input>
                    </el-descriptions-item>
                    <el-descriptions-item label="班级均分">
                        <el-input style="width:25%" maxlength="10" v-model.number="formData.class_score" clearable size="small"></el-input>
                    </el-descriptions-item>
                </el-descriptions>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="submit">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    schoolPerformanceArchivesList,
    schoolPerformanceInfo,
    schoolPerformanceView,
    schoolPerformanceAdd,
    schoolPerformanceEdit,
    schoolPerformanceDel,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                month: '',
                year: '',
                term: '',
                personal_score: '',
                class_score: '',
            },
            method_auth: {},
            editId: '',
            title: '',
            search: {
                month: '',
                year: '',
            },
            dialogVisible: false,
            tableHead: [
                { title: 'ID', prop: 'id', minWidth: '100', },
                { title: '学生姓名', prop: 'student_name', minWidth: '120', },
                { title: '班级', prop: 'class_name', minWidth: '100', },
                { title: '年份', prop: 'year', minWidth: '120', },
                { title: '学期', prop: 'term', minWidth: '150', },
                { title: '月份', prop: 'month_name', minWidth: '150', },
            ],
            tableData: [],
            tableInfo: [{
                type: 1,
                achievement: "",
                discipline: "",
                hold_post: "",
                home_school: "",
            }],
            tableInfoTwo: [{
                type: 2,
                achievement: "",
                discipline: "",
                hold_post: "",
                home_school: "",
            }],
            hash: '',
            gradeList: [],
            classList: [],
            studentFile_id: '',
            month_list: [],
            term_list: [],
            year_list: [],
            tableTitle: localStorage.getItem('studentFile_name'),
        };
    },
    mounted() {
        this.studentFile_id = localStorage.getItem('studentFile_id')
        this.getList();
        this.resView();
        this.getHash();
    },
    methods: {
        serchList() {
            this.pageNum = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getList();
        },
        async resView() {
            const res = await this.axios.post(schoolPerformanceView);
            if (res.data.code == 1) {
                this.year_list = res.data.data.year_list;
                this.term_list = res.data.data.term_list;
                this.month_list = res.data.data.month_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                student_id: this.studentFile_id,
                curr: this.pageNum,
                pageSize: this.pageSize,
                month: this.search.month,
                year: this.search.year,
            };
            const res = await this.axios.post(schoolPerformanceArchivesList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        addBtn() {
            this.title = '新增';
            this.dialogVisible = true;
            this.resetForm();
        },
        edit(row) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.resetForm();
            this.$nextTick(() => {
                this.getInfo(row.id)
            })
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'studentFile' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        //学生档案详情
        async getInfo(id) {
            const res = await this.axios.post(schoolPerformanceInfo, { archives_id: id });
            if (res.data.code == 1) {
                this.formData = res.data.data;
                this.tableInfo = [res.data.data.list[0]];
                this.tableInfoTwo = [res.data.data.list[1]];
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        resetForm() {
            this.formData = {
                month: '',
                year: '',
                term: '',
                personal_score: '',
                class_score: '',
            };
            this.tableInfo = [{
                type: 1,
                achievement: "",
                discipline: "",
                hold_post: "",
                home_school: "",
            }];
            this.tableInfoTwo = [{
                type: 2,
                achievement: "",
                discipline: "",
                hold_post: "",
                home_school: "",
            }];
        },
        async submit() {
            this.getHash();
            if (Number(this.formData.personal_score) <= 0 || Number(this.formData.class_score) <= 0) {
                this.$message({
                    showClose: true,
                    message: '个人积分、班级均分需为正整数',
                    type: 'error'
                });
                return
            }
            let data = {
                student_id: this.studentFile_id,
                month: this.formData.month,
                year: this.formData.year,
                term: this.formData.term,
                personal_score: this.formData.personal_score,
                class_score: this.formData.class_score,
                conduct_data: JSON.stringify([this.$refs.tableInfo.data[0], this.$refs.tableInfoTwo.data[0]]),
                hash: this.hash,
            }
            let api;
            if (this.title == '新增') {
                api = schoolPerformanceAdd
            } else {
                api = schoolPerformanceEdit;
                data.id = this.formData.id;
            }
            const res = await this.axios.post(api, data);
            if (res.data.code == 1) {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                });
                this.dialogVisible = false;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.getList();
            this.getHash();
        },
        delBtn(id) {
            this.$confirm('是否确定删除', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        archives_id: id,
                        hash: this.hash,
                    };
                    const res = this.axios.post(schoolPerformanceDel, datas).then(res => {
                        if (res.data.code == 1) {
                            this.$notify.success({
                                showClose: true,
                                message: res.data.msg,
                                type: 'success'
                            });
                            if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                this.page -= 1;
                            }
                            this.getList();
                        } else {
                            this.$notify.error({
                                title: '失败',
                                message: res.data.msg
                            });
                        }
                    });
                    this.getHash();
                })
                .catch(err => {
                    // this.$notify.info({
                    //     title: '取消',
                    //     message: '取消操作'
                    // });
                });
        },
    }
};
</script>

<style >
.studentFile .el-table__row--level-1 {
    background: #fdfafa !important ;
}
</style>
<style lang="scss" scoped>
.studentFile {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }
    .btn {
        width: 250px;
    }
    /deep/ .el-dialog {
        margin-top: 5vh !important;
    }
}
</style>



